                  <div class="pic">{{item.userName|titleSize}}</div>
                  <div class="text">{{item.userName}}</div>
                  <div class="time">{{item.sendTime}}</div>
                </div>
                <div class="content">
                  {{item.chatContent}}
                </div>

                <template v-if="item.appFileVoList && item.appFileVoList.length !== 0">
                  <div class="down-content" v-for="item in item.appFileVoList" :key="item.fileId">
                    {{item.fileName}}.{{item.fileType}}
                    <button class="down-btn" @click="downHandle(item.fileUrl)"></button>
                  </div>
                </template>
              </div>

              <div class="right-massage" v-if="item.msgPosition === 'right'">
                <div class="title">
                  <img class="del-icon" :src="del_icon" @click="delChat(item)"/>
                  <div class="time">{{item.sendTime}}</div>
                  <div class="text">{{item.userName}}</div>
                  <div class="pic">{{item.userName|titleSize}}</div>
                </div>
                <div class="content">
                  {{item.chatContent}}
                </div>
                <template v-if="item.appFileVoList &&item.appFileVoList.length !== 0">
                  <div class="down-content" v-for="item in item.appFileVoList" :key="item.fileId">
                    {{item.fileName}}{{item.fileType}}
                    <button class="down-btn" @click="downHandle(item.fileUrl)" v-preventReClick></button>
                  </div>
                </template>
              </div>


            </template>
          </div>
        </div>

        <div class="dialog-input" ref="dialogInput">
          <el-input
            autofocus="true"
            class="text-input"
            type="textarea"
            :autosize="{ minRows: 3, maxRows: 5}"
            placeholder="..."
            v-model="chatContent"
            @keydown.native="handleKeyCode($event)"
          >
          </el-input>
          <div class="btn-group">
            <div slot="footer" class="dialog-footer" style="width: 50%;">
              <el-upload action="#"
                         class="upload-box upload-box-width"
                         :auto-upload="false"
                         :file-list="fileTemp"
                         :on-remove="handleRemove"
                         :on-change="fileChange"
              >
                <el-button type="info" class="update-files-btn"><i class="el-icon-upload el-icon--right"></i>
                </el-button>
              </el-upload>
            </div>
            <div style="display: flex;align-items: center;">
              <span class="send-tap">EnterCtrl+Enter</span>
              <el-button class="send-btn" type="primary" @click="sendData"> </el-button>
            </div>
          </div>
        </div>

      </div>
