      // , , false  Promise  reject
      beforeUploadFile(file) {
        const extension = file.name.substring(file.name.lastIndexOf('.') + 1)
        const size = file.size / 1024 / 1024
        this.fileTemp = ''
        this.fileList = []
        if (extension !== 'xlsx') {
          this.$message.warning('.xlsx')
          this.fileTemp = ''
          this.fileList = []
        }
        if (size > 10) {
          this.$message.warning('10M')
          this.importFileTemp = ''
          this.importFileList = []
        }
      },

      // 
      importFileChange(file, fileList) {
        this.importFileTemp = ''
        this.importFileList = []

        this.importFileTemp = file.raw
        if (this.importFileTemp) {
          const extension = file.name.substring(file.name.lastIndexOf('.') + 1)
          if (extension !== 'xlsx') {
            this.$message.warning('.xlsx');
            this.importFileTemp = '';
            this.importFileList = []
          } else {
            this.importMaterielList(this.importFileTemp)
          }
        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
          this.importFileTemp = ''
          this.importFileList = []
        }
      },

      importMaterielList(obj){
        const formData = new FormData()
        formData.append('file', obj)
        importCrmCompanyInfoList(formData).then(res => {
          if (res.code == 200) {
            this.importFileTemp = ''
            this.importFileList = []
            this.$message({
              type: 'success',
              message: res.message
            })

            this.exportFailShow = false;
            this.exportFailMsg = {};

            let taskMsg = {
              taskId: res.data.taskId
            };

            this.$store.commit('SET_CUSTOMER_TASKMSG', taskMsg);
            this.exportShow = true;
            this.getTaskInfo();

            this.getList()
          } else {
            this.importFileTemp = ''
            this.importFileList = []
            this.$message({
              type: 'warning',
              message: res.message
            })
          }
        })
      },




