        }
      }

      /**/

      &::-webkit-scrollbar {
        width: 8px;
        height: 1px;
      }

      /**/

      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: #69CCFF;
      }

      /**/

      &::-webkit-scrollbar-track {
        border-radius: 10px;
        background: #CDEEFF;
      }
    }

    .dialog-input {
      height: 120px;
      border: 1px solid #37BBFF;

      /deep/ .el-textarea__inner {
        border: 0;
        resize: none;
      }

      .btn-group {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 0 15px;

        /deep/ .el-button {
          border: 0;
        }

        .update-files-btn {
          background: linear-gradient(180deg, rgba(152, 177, 250, 1) 0%, rgba(88, 131, 255, 1) 100%);
        }

        .send-tap {
          white-space: nowrap;
          font-size: 14px;
          color: #00A5FF;
          margin-right: 10px;
        }

        .send-btn {
          background: linear-gradient(180deg, rgba(105, 204, 255, 1) 0%, rgba(5, 170, 255, 1) 100%);
        }
      }
    }
  }

  .el-button.is-disabled{
    color: #fff;
    background-image: none;
    background-color: #C0C4CC;
  }

  .add-sale-modal {
    /deep/ .el-dialog__header {
      border-bottom: 1px solid #ccc;
    }

    .add-sale-table {
      .flex-center {
        display: flex;
        align-items: center;

        i {
          font-size: 14px;
          color: #00A5FF;
          cursor: pointer;
        }
      }

      .icon-sale-del {
        color: red;
        cursor: pointer;
        font-size: 14px;
      }
    }
  }

  .star{
    color: #ff3d4c !important;
    position: absolute;
    left: -8px;
  }


</style>



